@import url(https://fonts.googleapis.com/css?family=Poller+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poller+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fb6542; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

/* Typgraphy */

.Account {
  display: flex;
  align-items: center;
  flex-wrap: wrap; }
  .Account img {
    max-width: 225px;
    margin-right: 15px; }
  .Account input {
    height: 75px;
    flex: 1 1; }

input[type=submit] {
  border: none;
  background-color: #ffbb00;
  font-family: 'Poller One', cursive; }

.linkContainer {
  display: grid;
  grid-template-columns: 30% auto;
  grid-gap: 5px 50px;
  align-items: center; }
  .linkContainer img {
    width: 100%; }

.Printout {
  display: grid;
  height: 297mm;
  width: 210mm;
  grid-template-columns: 12.5% auto 12.5%;
  grid-template-rows: 30mm 1fr 1fr 30mm;
  grid-gap: 2px; }
  .Printout .section {
    background-color: #ffffff;
    border: 1px solid #f5f5f5;
    display: flex;
    justify-content: center;
    align-items: center; }
  .Printout .Accounts {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: auto 1fr; }
    .Printout .Accounts .accountLinksContainer {
      height: 100%;
      display: grid;
      justify-content: center; }
  .Printout .Instructions {
    height: 100%;
    width: 100%; }
  .Printout .left {
    grid-column-start: 3;
    grid-row: 1 / span 4; }
  .Printout .right {
    grid-row: 1 / span 4; }
  .Printout .flip {
    -webkit-transform: scale(-1, -1);
    transform: scale(-1, -1); }

.App {
  text-align: center; }
  .App .headline {
    font-family: 'Poller One', cursive;
    color: #ffbb00; }

.App-logo {
  height: 40vmin; }

